@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
.links {
  position: fixed;
  width: 100%;
  top: 0px;
}

.links ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
body {
  font-weight: 400;
  font-family: 'Rubik';
  background-color: black;
  color: white;
}

.Intro{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
  margin-top: 200px;
  color: white;
}

.intro {
  margin-top: 120px;
  font-size: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 400;
  margin-left: 50px;
}

.Typewriter {
  display: inline-block;
  font-weight: 500;
}

.Navbar {
  margin-top: 120px;
  margin-right: 50px;
}

.Navbar ul{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  list-style: none;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 50px;
  margin: 0px;
}

.Navbar a{
  text-decoration: none;
  color: white;
  font-weight: 300;
  text-transform: uppercase;
  padding-right: 100px;
}

.IntroContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.links ul {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  justify-self:right;
  margin-right: 50px;
}

.links a{
  color: white;
  font-size: 50px;
  padding: 10px;
}


.links {
    background-color: black;
    padding-bottom: 20px;
}
.Work {
    padding-bottom: 500px;
}

.WorkEntries {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0px 200px 0px 200px;
    padding: 40px 5px 0px 5px;
    font-size: 25px;
}

.WorkEntries h1 {
    color: #cc0000;
    font-size: 45px;
    font-weight: 500;
    margin-bottom: 20px;
}

.WorkEntries .company, .timeline {
    font-size: 23px;
    font-weight: 400;
    font-style: italic;
    padding: 5px;
}

.WorkEntries.Introduction {
    margin: 200px 200px 0px 200px;
}

.WorkEntries h2{
    font-weight: 400;
    font-size: 35px;
}
.WorkEntries ul {
    list-style: circle;
}

.WorkEntries h3{
    font-weight: 400;
    font-size: 30px;
}

.title {
    font-size: 100px;
    font-weight: 400;
    position: fixed;
    top: 0px;
    left: 200px;
}

.Experiences {
    font-size: 50px;
}

a {
    color: white;
}

.WorkEntries li {
    padding: 2px;
}

.WorkEntries .description {
    margin-top: 40px;
}


